
import './App.css';
import me from './img/me.jpg'
import Social from './components/social'
function App() {

  let r = [];
  for(let i = 0;i<10;i++)
    r.push(i);
  r =r.map(v  => <p key={v}>{v}</p>)
  return (
    <div className="main">
    <h1>Rafael Campos</h1>
    <img src={me} className="me" />
    <p>Android Developer</p>
    <p>Software developer, experienced with Android development. </p>
    {r}
    <div className="social">
      <Social/>
    </div>
    </div>
  );
}

export default App;
